import React, { useState } from 'react';
import './NavBar.css';

var Navbar = () => {
    const [checked , setChecked] = useState(false);
    const toggle =()=>{
    setChecked(value=> !value)
   
}


    return (
        <div className={"header "+ (checked?"is-active":"")}>
             <div className="container">
                <nav>
                    
                        <div className="logo">
                        </div>
                        <div className={"navbar " + (checked?"is-active":"")} >
                        <a className="navBtn" href="/">home</a>
                        <a className="navBtn" href="#services">services</a>
                        <a className="navBtn" href="#">Our works</a>
                        <a className="navBtn" href="#">contact</a>
                        <a className="navBtn" href="#aboutMe">about</a>
                        </div>
                   
                   <div className={"menustar " + (checked?"is-active":"")}  >
                       <span onClick={toggle}></span>
                       <span onClick={toggle}></span>
                       <span onClick={toggle}></span>
                   </div>
                  </nav>
            </div>
        </div>
    );
}

export default Navbar;
